// src/services/productService.js

import api from './api';

//   API    api.baseURL     :
const BASE_PATH = '/api/products';

/**
 *   
 * @param {File} file -   
 * @returns {Promise<string>} - Promise  resolves   URL  
 */
export async function uploadProductImage(file) {
  if (!file) {
    throw new Error('  ');
  }
  const formData = new FormData();
  formData.append('file', file);

  try {
    const res = await api.post(`${BASE_PATH}/upload`, formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    });
    //    { url: "http://.../uploads/filename.jpg" } 
    return res.data.url;
  } catch (err) {
    console.error('   :', err);
    //       :
    throw err;
  }
}

/**
 *   
 * @param {{name: string, description?: string, price: number, productType: string, imageUrl?: string}} productData
 * @returns {Promise<Object>} -     
 */
export async function createProduct(productData) {
  try {
    const res = await api.post(`${BASE_PATH}`, productData);
    return res.data;
  } catch (err) {
    console.error('   :', err);
    throw err;
  }
}

/**
 *    (     )
 * @param {string?} type -   'PHONE','REPAIR','LCD','SMALLPARTS','ACCESSORIES'
 * @returns {Promise<Array>}
 */
export async function getAllProducts(type) {
  try {
    const params = {};
    if (type) {
      params.type = type;
    }
    const res = await api.get(`${BASE_PATH}`, { params });
    return res.data;
  } catch (err) {
    console.error('   :', err);
    throw err;
  }
}

/**
 *      ID
 */
export async function getProductById(id) {
  try {
    const res = await api.get(`${BASE_PATH}/${id}`);
    return res.data;
  } catch (err) {
    console.error('     id=', id, err);
    throw err;
  }
}

/**
 *  
 */
export async function updateProduct(id, productData) {
  try {
    const res = await api.put(`${BASE_PATH}/${id}`, productData);
    return res.data;
  } catch (err) {
    console.error('    id=', id, err);
    throw err;
  }
}

/**
 *  
 */
export async function deleteProduct(id) {
  try {
    await api.delete(`${BASE_PATH}/${id}`);
  } catch (err) {
    console.error('    id=', id, err);
    throw err;
  }
}
